import jQuery from 'jquery'
window.jQuery = window.$ = jQuery


const WOW = require('wowjs');
window.wow = new WOW.WOW

 //animation
window.wow.init();



 
 
//carousel
jQuery(document).ready(function($){

    $('#backgroundslider').owlCarousel({
        items:1,
        loop:true,
        nav:false,
        dots:false,
        responsiveClass:true,
        mouseDrag:false,
        touchDrag:false,
        pullDrag:false,
        freeDrag:false,
        autoplay:true,
        animateOut: 'fadeOut'
    })

    $('#popularcourse, #masterprogram').owlCarousel({
        items:4,
        loop:false,
        nav:false,
        autoplay:true,
        responsiveClass:true,
        responsive:{
            0:{
                items:1
            },
            560:{
                items:2
            },
            991:{
                items:3
            },
            1200:{
                items:4
            }
        }
    })

    $('#clientslide').owlCarousel({
        items:5,
        loop:true,
        nav:false,
        responsiveClass:true,
        responsive:{
            0:{
                items:2
            },
            560:{
                items:3
            },
            991:{
                items:4
            },
            1200:{
                items:5
            }
        }
    })

    $('#enquiryModal').modal('show');

});


